import React, { Component } from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';

const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing.unit * 2,
    },
    title: {
        flexGrow: 1,
    },
});

class Home extends Component {
    state={
        menu:false
    }

    handleMenu(e){
        let menuValue = this.state.menu;
        this.setState({
            menu: !menuValue
        })
    }
    render() {


        return (
            <div className={this.props.classes.root}>
                <AppBar position="static">
                    <Toolbar>
                        <IconButton edge="start" className={this.props.classes.menuButton} color="inherit" aria-label="Menu">
                            <MenuIcon onClick={this.handleMenu.bind(this)} />

                        </IconButton>
                        <Typography variant="h6" color="inherit" className={this.props.classes.title}>
                            Data Wallet
                            </Typography>
                        <Button color="inherit">Login</Button>
                    </Toolbar>
                </AppBar>
                <SwipeableDrawer 
                    open={this.state.menu}
                    onClose={this.handleMenu.bind(this)}
                    onOpen={this.handleMenu.bind(this)}
                >
                    <List >
                        {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
                            <ListItem onClick={this.handleMenu.bind(this)} button key={text}>
                                <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
                                <ListItemText primary={text} />
                            </ListItem>
                        ))}
                    </List>
                </SwipeableDrawer>
            </div>

        )
    }
}

Home.props = {
    classes: PropTypes.object.isRequired
}

export default withStyles(styles)(Home);